.header {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fff;
  text-align: right;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99999;
}
.header .header-link {
  display: flex;
  top: 100px;
  left: 40px;
  flex: 1 1;
  margin-left: 40px;
}
.header .header-link img {
  width: 35px;
  height: 35px;
}
.header .header-nav {
  height: 60px;
  line-height: 60px;
  width: auto;
  flex: 10 1;
  text-align: right;
  margin-right: 40px;
  font-size: 17px;
}
.header .header-login {
  flex: 1 1;
  margin-right: 40px;
}
.header .header-login .header-login-button {
  font-size: 17px;
}
